    .cajon-b.color-acento-contenido.cajon-pr.p-4.mb-5
      p Entindase negocio como la fuente de actividades de servicios, industriales, comerciales, ganadera y agricultura, construccin, transporte, financieros y profesionales que ofrece una empresa u organizacin apoyados en la promocin de la misin, la visin, los objetivos generales, las polticas y valores organizacionales. 
      p Las empresas nacen de una idea de negocio en la que se lleva a cabo una observacin de la realidad del mercado, un anlisis de los factores internos y externos que influyen en la toma de decisiones para invertir en la idea, s esta es viable se puede constituir ante la Cmara de Comercio y la DIAN con el fin de empezar a operar de manera legal. 
      p.mb-0 Una empresa puede ser con fines de lucro cuando genera una idea de negocio donde se obtienen ingresos y puede ser sostenibilidad en el mercado a travs de lo que produce o vende, pero tambin se consolidan empresas sin nimo de lucro, como: las iglesias, las fuerzas militares, las fundaciones y las ONG, que benefician a la comunidad mediante el servicio que prestan sin recibir una remuneracin por lo que hacen.

