        p La mayora de los negocios nacen al hacer la identificacin de necesidades por satisfacer en un sector de la poblacin, lo cual genera ganancias para el emprendedor, pero esto solo se da si se parte de una investigacin del producto, del mercado y de la competencia, entre otros factores, lo cual lleva a la conclusin de factibilidad del mismo.
        p.mb-0 Ahora bien, las ideas de negocios son ejecutadas basadas en el estudio de los modelos de negocios ya existentes, basta con revisar la competencia y lo que ha dado resultado para deducir el modelo de negocio que necesita para generar inversin y financiamiento. Por lo anterior, es importante profundizar en la importancia del modelo de negocio como eje central para una nueva empresa o la ampliacin de una lnea de producto de una empresa existente.
      .col-lg-5.col-10.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
