    p.mb-5(data-aos="fade-right") El modelo de negocio permite desarrollar inversiones para obtener ganancias a travs de los diferentes servicios y productos que la empresa comercializa, para utilizar un modelo de negocios, la organizacin gestiona un plan comercial teniendo en cuenta los siguientes elementos:

    .row.justify-content-center.mb-5
      .col-lg-10.tarjeta.BG02.p-4
        .row.justify-content-center
