    p(data-aos="fade-right") Los modelos de negocios son necesarios para hacer negocios de manera rentable, focalizndose en las estrategias de las que dispone la empresa. Aqu es vital hablar de un trmino clave: la inversin, es decir, la persona o personas que estn dispuestos a invertir, por lo que cualquier toma de decisiones parte del anlisis de la idea de negocio.
    p.mb-5(data-aos="fade-right") Para identificar si esta es rentable se deben considerar los siguientes elementos:

    .row.justify-content-center.mb-5
