    p(data-aos="fade-right") Por ello, un inversionista o analista del mercado debe analizar y evaluar el triunfo del modelo de negocio de la empresa a travs de las ganancias totales, del flujo de caja, de los ingresos netos y de la utilidad bruta, para tener conocimientos claros y exactos de las ganancias reales que ofrece el negocio.
