      p.mb-5(data-aos="fade-right") Para llevar a cabo el diseo de un negocio, es importante realizar un esquema o representacin grfica en el que se describan los aspectos claves para llegar al cliente, los medios que se utilizarn para darse a conocer y las cifras que le indican que el negocio es viable.

      .row.justify-content-center.mb-5
        .col-lg-10
          p(data-aos="fade-right") El modelo de negocio se plantea en el proceso de planeacin estratgica, all se formulan los objetivos generales de la empresa y las estrategias que permitan cumplir con ellos, por ende, el modelo de negocio es ms una estrategia que se utiliza para expresar visualmente la importancia que tendr la empresa para el cliente.
          p(data-aos="fade-right") En la representacin de la informacin del modelo de negocio se trabajan los elementos fundamentales del negocio, siendo conscientes de una estructura coherente para expresar fcilmente el concepto de la idea de negocio.
          p.mb-5(data-aos="fade-right") Es por ello que en la mayora de las empresas el Lienzo Canvas o modelo Canvas  es el esquema que se utiliza con frecuencia para gestionar estratgicamente la comprensin y las conexiones entre lo que se desea y cmo convertirlo en negocio, adems ofrece informacin efectiva de las probabilidades de lo que puede ser el negocio.

