    p.mb-5(data-aos="fade-right") Es un documento, generalmente de una pgina, en el que se grafica en detalle los puntos clave para llevar la idea al mercado, asimismo, es referenciada como una hoja de ruta que desglosa los elementos estratgicos para llevar a buen puerto el producto con xito. El lienzo de modelo condensa los siguientes elementos:

    .row.justify-content-center.mb-5
