        p(data-aos="fade-right") Es fundamental para cualquier negocio, es el concepto fundamental del intercambio de valor entre su negocio y sus clientes. La propuesta de valor siempre va a estar condicionada a la percepcin de valor para el cliente, es as como este se motiva a efectuar la compra o ms bien establece la razn por la cual es incitado a comprar. 
        p.mb-5(data-aos="fade-right") Se deben observar los segmentos de los clientes, la utilidad y prctica, explorar dnde los productos o servicios resuelven necesidades para los clientes o le ofrecen ventajas con la compra, la innovacin, el tipo de valor que brindar al cliente, por lo cual se deben tener en cuenta los siguientes criterios para que la competencia no imite tan fcilmente un producto: 

    .tarjeta--container.row.mb-5
