    p.mb-5(data-aos="fade-right") El foco debe estar en los clientes que estn interesados en el producto o servicio, que realmente lo necesitan y desean adquirirlo. Enfocarse en segmentos de mercados amplios disminuye la efectividad y aumenta la prdida de tiempo y dinero en personas que no son ni sern los clientes potenciales en el presente y futuro.

    .row.justify-content-center.mb-5
      .col-lg-10.tarjeta.BG05.py-4.px-lg-5.px-4.mb-5
        p(data-aos="fade-right") Para lograr la segmentacin de los clientes debe: 
