    p(data-aos="fade-right") Para la recopilacin de estos datos lo puede hacer directamente con el cliente o utilizando bases de datos, tambin se pueden analizar los perfiles de las redes sociales para extraer esta informacin para el beneficio del negocio, ya que la finalidad es establecer hacia dnde se dirigirn los esfuerzos e intereses relacionados con los posibles clientes compradores.
    p.mb-5(data-aos="fade-right") En este punto es fundamental conocer el comportamiento del consumidor y su conducta de compra, ya que estos pueden ser acciones de interaccin entre el producto, el consumidor y las redes o aplicaciones. 

    ul.lista-ul--color.mb-5
      li(style="margin-bottom: 2px" data-aos="fade-right")
        i.fas.fa-check-circle.tcp
        .h4.tcp.mb-0 Canales
    
    p.mb-5(data-aos="fade-right") Establecen la relacin de la empresa con el cliente a travs de diferentes puntos de contacto, dicho en otras palabras, son los instrumentos por donde pasa el producto o servicios hasta llegar al cliente final, estos pueden ser:

    AcordionA.mb-5(tipo="b" clase-tarjeta="BGR02" data-aos="zoom-in")
      .row.justify-content-center.align-items-center(titulo="De distribucin")
        .col-lg-8.order-lg-1.order-2
          p Los canales de distribucin son los medios necesarios para entregar el producto al consumidor final, entendiendo que este puede ser minorista (tiendas de barrio) o mayoristas (almacenes de cadenas y supermercados), tambin debe identificarse si el canal de distribucin es directo (relacin vertical entre el fabricante y el cliente) o si es indirecto (comprende de varios intermediarios antes de que el producto llegue a su destino final).
          p.mb-0 De lo anterior, se puede deducir que, si la empresa posee ms intermediarios en el canal de distribucin, el precio del producto incrementa; por el contrario, si opera con un solo canal directo, el precio del producto puede ser ms bajo.
        .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4
          img(src='@/assets/curso/tema1/img19.png' alt="Imagen decorativa")
      .row.justify-content-center.align-items-center(titulo="De comunicacin")
        .col-lg-8.order-lg-1.order-2
          p Los canales de comunicacin son utilizados para enviar mensajes a los clientes, pues de esta manera llaman su atencin y dan a conocer las promociones y descuentos, adems de atraer su atencin, potencian la experiencia con la marca, por lo que el reconocimiento e impacto en las ventas son las principales ventajas de hacer uso de ellos.
          p Dentro de los canales de comunicacin de mayor auge en la era digital se encuentran las redes sociales a travs de las cuales se puede compartir contenidos de inters (imgenes, videos, anuncios) que acercan al cliente para responder y comentar.
          p.mb-0 Otro de los canales de comunicacin con mayor incremento es el uso de mensajera instantnea, tambin es importante que la empresa disponga de portales web y oficinas de servicio al cliente.
        .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4
          img(src='@/assets/curso/tema1/img20.png' alt="Imagen decorativa")
      .row.justify-content-center.align-items-center(titulo="De venta")
        .col-lg-8.order-lg-1.order-2
          p Los canales de ventas son la forma o el medio de venderle al cliente, son utilizados como mecanismos para cerrar ventas, las cuales pueden ser al por mayor, porque llegan a muchas personas y tienen la influencia de llegar a todas partes incluidos los minoristas y los revendedores, por lo que se requiere flujo de capital para hacer uso de este tipo de canales.
          p.mb-0 En adicin, es indispensable que se cuente con inversin en inventario, con el fin de que se muevan constantemente. En contraposicin, al por menor se obtiene informacin ms detallada del cliente y una conexin ms cercana entre comprador y vendedor.
        .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4
          img(src='@/assets/curso/tema1/img21.png' alt="Imagen decorativa")

    .BGM06.px-md-5.px-4.mb-5
      .row.justify-content-center.mb-5
        .col-lg-4.col-8.mb-lg-0.mb-4(data-aos="fade-right")
          img(src='@/assets/curso/tema1/img22.svg' alt="Imagen decorativa")
        .col-lg-8(data-aos="fade-left")
          ul.lista-ul--color.mb-5
            li(style="margin-bottom: 2px" data-aos="fade-right")
              i.fas.fa-check-circle.tcp
              .h4.tcp.mb-0 Relacin con clientes
          p Se definen como la forma en que la empresa interacta con los clientes, de manera virtual, a travs de un telfono o atencin de manera personal. Las relaciones con los clientes describen los tipos de contactos que la empresa establece con segmentos de clientes especficos, permitiendo aclarar los puntos de responsabilidades entre el negocio y el cliente y los modos utilizados para relacionarse entre ellos.
          p.mb-0 Por lo anterior, la empresa delimita el tipo de relacin que quiere establecer con cada grupo de clientes, estas se funden a travs de sus diferentes canales, las cuales tambin pueden variar desde personales hasta virtuales, o cortas hasta a largo plazo, y pueden apuntar a adquirir clientes, retener clientes o impulsar las ventas. El tipo de relaciones con los clientes establece la influencia que se tiene en la experiencia del cliente.
      
      ul.lista-ul--color.mb-5
        li(style="margin-bottom: 2px" data-aos="fade-right")
          i.fas.fa-check-circle.tcp
          .h4.tcp.mb-0 Actividades clave
      p(data-aos="fade-right") Acciones en las que se determina cmo llegar a desarrollar la propuesta de valor, entre las actividades clave se encuentran:
      
      .tarjeta.tarjeta--blanca.p-4.mb-5
        ul.lista-ul--color
          li(style="margin-bottom: 2px" data-aos="fade-right")
            i.fas.fa-chevron-circle-right.tcp
            | El tiempo empleado.
          li(style="margin-bottom: 2px" data-aos="fade-right")
            i.fas.fa-chevron-circle-right.tcp
            | Las estrategias.
          li(style="margin-bottom: 2px" data-aos="fade-right")
            i.fas.fa-chevron-circle-right.tcp
            | La distribucin del producto.
          li(style="margin-bottom: 2px" data-aos="fade-right")
            i.fas.fa-chevron-circle-right.tcp
            | La calidad en el diseo.
          li(style="margin-bottom: 2px" data-aos="fade-right")
            i.fas.fa-chevron-circle-right.tcp
            | La produccin de grandes cantidades del producto. Estas acciones son importantes para la estructura, el modelo de negocio y la propuesta de valor.

      .row.justify-content-center.align-items-center.mb-5
        .col-lg-8.order-lg-1.order-2(data-aos="fade-right")
          p Las tareas esenciales que se realizan para alcanzar los segmentos de clientes y crear fuentes de ingresos pueden variar segn el modelo de negocio. Estas acciones pueden ser crticas, ya que deben ejecutarse para mantener su ventaja competitiva, es decir, un producto que ingrese a la lnea de produccin es evaluado e investigado desde su diseo, costos y el tiempo que tarda en producirse.
          p Para desarrollar nuevos productos es muy conveniente ejecutar una investigacin y revisar los resultados para orientar el desarrollo.
          p.mb-0 Unas de las tareas en las que hoy en da se hace hincapi es en la comprensin de la innovacin y las nuevas tendencias del mercado  para garantizar as que se cumplan las expectativas de los diferentes entornos.
        .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
          img(src='@/assets/curso/tema1/img23.png' alt="Imagen decorativa")

      .row.justify-content-center
        .col-lg-10.BParalel.px-5
          p(data-aos="fade-right") Por otro lado, analizar las fortalezas y debilidades del producto es propicio para desarrollar otro punto clave, la estrategia, que colma los vacos del mercado satisfaciendo la necesidad a raz de nuevos productos. Es importante observar los elementos relacionados para comprender las actividades que son fundamentales para mantener buenas relaciones con el cliente, para aportar a la propuesta de valor y aprovechar los flujos de ingresos.
          p.mb-0(data-aos="fade-right") Por lo anterior, es indispensable que el cuadro de las actividades claves sea revisado constantemente, ya que en la medida que el negocio crezca ser necesario incluir o modificar las tareas esenciales.

    ul.lista-ul--color.mb-5
      li(style="margin-bottom: 2px" data-aos="fade-right")
        i.fas.fa-check-circle.tcp
        .h4.tcp.mb-0 Recursos clave
    
    p(data-aos="fade-right") Son los elementos indispensables para alcanzar las actividades claves del negocio, estos pueden ser: econmicos, humano y fsicos, que dependen en gran medida de los medios a utilizar basados en los recursos clave que se necesiten en los canales, en la propuesta de valor, en la relacin con el cliente y en los ingresos.
    p.mb-5(data-aos="fade-right") Es importante dentro de los recursos claves asignar los espacios necesarios, como: 

    .row.justify-content-center.mb-5
      .col-lg-10.mb-4
        .row.justify-content-center.mb-4
          .col-lg.col-sm-4.col-6.mb-lg-0.mb-4(data-aos="flip-up")
            .tarjeta--boton.color-primario.h-100.p-4
              .row.justify-content-center.mb-4
                .col-9
                  img(src='@/assets/curso/tema1/img24.png' alt="Imagen decorativa")
              h4.text-white.text-center.mb-0 Edificios para su funcionamiento
          .col-lg.col-sm-4.col-6.mb-lg-0.mb-4(data-aos="flip-up")
            .tarjeta--boton.color-primario.h-100.p-4
              .row.justify-content-center.mb-4
                .col-9
                  img(src='@/assets/curso/tema1/img25.png' alt="Imagen decorativa")
              h4.text-white.text-center.mb-0 Contar con la maquinaria requerida
          .col-lg.col-sm-4.col-6.mb-lg-0.mb-4(data-aos="flip-up")
            .tarjeta--boton.color-primario.h-100.p-4
              .row.justify-content-center.mb-4
                .col-9
                  img(src='@/assets/curso/tema1/img26.png' alt="Imagen decorativa")
              h4.text-white.text-center.mb-0 Contar con el equipo que se requiere
          .col-lg.col-sm-4.col-6.mb-lg-0.mb-4(data-aos="flip-up")
            .tarjeta--boton.color-primario.h-100.p-4
              .row.justify-content-center.mb-4
                .col-9
                  img(src='@/assets/curso/tema1/img27.png' alt="Imagen decorativa")
              h4.text-white.text-center.mb-0 Vincular el talento humano capacitado 
          .col-lg.col-sm-4.col-6.mb-lg-0.mb-4(data-aos="flip-up")
            .tarjeta--boton.color-primario.h-100.p-4
              .row.justify-content-center.mb-4
                .col-9
                  img(src='@/assets/curso/tema1/img28.png' alt="Imagen decorativa")
              h4.text-white.text-center.mb-0 Contar con el capital financiero (efectivo y crdito), entre otros elementos
      .col-lg-10
        .tarjeta.BG05.p-4(data-aos="fade-right")
          p Tambin es importante definir qu tipo de materiales se necesitan, qu tipo de equipo se requiere y los perfiles de personas que se requiere emplear, ya que juegan un papel crucial al momento de dar vida a la propuesta de valor para el segmento de clientes elegido y definen lo mnimo que necesita para entregar a los clientes.
          p.mb-0 Si la empresa no tuviese algunos de los recursos que se necesitan, tiene la opcin de arrendar o solicitarlos a los aliados claves para poseerlos y proporcionar de manera efectiva el acceso a los recursos.

    ul.lista-ul--color.mb-5
      li(style="margin-bottom: 2px" data-aos="fade-right")
        i.fas.fa-check-circle.tcp
        .h4.tcp.mb-0 Aliados clave

    p(data-aos="fade-right") Ayudan a que el modelo de negocio fluya, pues permiten desarrollar relaciones con otras empresas comerciales, como: los fabricantes, los socios y los proveedores, que permitirn que el negocio tenga la fortaleza suficiente en aquellas reas en las que la empresa por s sola es ineficaz.
    p.mb-5(data-aos="fade-right") Establecer las relaciones con otras empresas es empoderar el negocio en aquellos eslabones en los que es poco efectivo.

    .bloque-texto-g.color-primario.p-3.p-sm-4.p-md-5.mb-5
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/tema1/img29.jpg')})`}" data-aos="fade-right"
      )
      .bloque-texto-g__texto.p-4(data-aos="fade-left")
        p.mb-0 Muchas organizaciones hacen uso de alianzas estratgicas  entre compaas que no generan ningn peligro entre s, es decir, que no son competencia la una para la otra para beneficiarse mutuamente.<br><br>
          | Dentro de los aliados clave, tambin se encuentra la relacin entre empresas competitivas en el mercado, quienes recurren a esta simbiosis para trabajar juntas, sensibilizar el mercado compartido y ganar nuevos clientes. 

    .row.justify-content-center.mb-5
      .col-lg-10
          p.mb-0(data-aos="fade-right") Otra unin que pueden celebrar las empresas es aquella en la que desarrollan nuevos negocios, es decir, crean una nueva organizacin completamente diferente en la que las dos obtengan una rentabilidad mucho mayor que la que obtienen cuando operan por separado. Por ltimo, una de las asociaciones ms comunes, es la relacin de la empresa con los proveedores, que tiene como objetivo: generar calidad, confianza y respaldar los suministros entre los roles de proveedor y comprador.

    ul.lista-ul--color.mb-5
      li(style="margin-bottom: 2px" data-aos="fade-right")
        i.fas.fa-check-circle.tcp
        .h4.tcp.mb-0 Estructura de costes
    
    p(data-aos="fade-right") Consiste en establecer menos gastos para generar productos ms accesibles, por ello, es recomendable minimizar cualquier costo siempre que sea posible. Ahora bien, se deben identificar todos los costos que se quieren disminuir para generar productos a bajo costo y de esta manera entrar a competir en el mercado, sin embargo, se debe tener cuidado, ya que puede convertirse en una bomba de tiempo que lo puede llevar al fracaso, puesto que para algunas empresas los precios bajos son ilgicos. 
    p(data-aos="fade-right") En tal sentido, se recomienda revisar y analizar la propuesta de valor, para que ese sea el fuerte del negocio y no el precio bajo que pudiese ofrecer al cliente. Los modelos de negocios son definidos por la propuesta de valor, por lo que estn menos preocupados por los costos de transaccin y se enfocan en tener un alto nivel de personalizacin y desarrollado segn las preferencias de los clientes.
    p.mb-5(data-aos="fade-right") A continuacin, se explican los costos que se deben analizar para la sostenibilidad del modelo negocio y otros factores a tener en cuenta:

    TabsC.color-acento-botones.mb-5(data-aos="zoom-in")
      .BG07.p-4(titulo="Costos fijos")
        .row.justify-content-center.mt-4
          .col-lg-4.col-8.mb-lg-0.mb-4
            img(src='@/assets/curso/tema1/img30.png' alt="Imagen decorativa")
          .col-lg-8
            p Son aquellos que tienen un lmite de tiempo y que su costo permanece estable durante un buen periodo. Estos no se relacionan con la cantidad de productos o servicios que ejecute. 
            p.mb-0 Ejemplo: los sueldos de los colaboradores, el arriendo del local, almacn y/o empresa.
      .BG07.p-4(titulo="Costos variables")
        .row.justify-content-center.align-items-center.mt-4
          .col-lg-8.order-lg-1.order-2
            p.mb-0 En estos costos influye la oferta y la demanda, ya que la variabilidad de los costos se sostiene en el mayor o menor volumen de produccin, dicho en otras palabras, si no hay demanda del producto o servicio, por ende, no se necesita producir y esto hace que los costos varen, por lo que estos son difciles de prever, debido a esto una macroempresa tiene un costo unitario ms bajo que una microempresa.
          .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4
            img(src='@/assets/curso/tema1/img31.png' alt="Imagen decorativa")
      .BG07.p-4(titulo="Economa de escala")
        .row.justify-content-center.mt-4
          .col-lg-4.col-8.mb-lg-0.mb-4
            img(src='@/assets/curso/tema1/img32.png' alt="Imagen decorativa")
          .col-lg-8
            p.mb-0 Las empresas con altas demandas de produccin se benefician de un menor costo por unidad, ya que en los volmenes se distribuyen los costos fijos uniformemente en los pedidos pequeos. Esta estructura de costos es ms comn en las grandes empresas que realizan compras a proveedores al por mayor.
      .BG07.p-4(titulo="Economa de alcance")
        .row.justify-content-center.mt-4
          .col-lg-4.col-8.mb-lg-0.mb-4
            img(src='@/assets/curso/tema1/img33.png' alt="Imagen decorativa")
          .col-lg-8
            p Dentro de las ventajas de la economa de alcance, se tiene:
            ol.lista-ol--cuadro
              li 
                .lista-ol--cuadro__vineta(style="background-color: #6DEBFF")
                  span(style="color: #12263F") 1
                | Hay menos riesgo en un negocio que vende ms de un producto y/o segmenta varios mercados, puesto que la empresa tendr ms alternativas para sostenerse si se ve afectada por el mercado, mientras acomoda y encamina sus estrategias para recuperarse. 
              li 
                .lista-ol--cuadro__vineta(style="background-color: #6DEBFF")
                  span(style="color: #12263F") 2
                | La flexibilidad para fusionar productos para evitar el desperdicio de materia prima con el fin de tener mayor capacidad de respuesta ante los cambios del mercado.

    .row.justify-content-center.mb-5
      .col-lg-10
          p.mb-0(data-aos="fade-right") Por ltimo, la estructura de costos es ms fcil de calcular si previamente se han identificado los aliados, actividades y recursos clave para garantizar la sostenibilidad del negocio en la medida de un largo plazo y no fracasar en los primeros aos por no haber establecido los costos que requera el modelo de negocio.

    ul.lista-ul--color.mb-5
      li(style="margin-bottom: 2px" data-aos="fade-right")
        i.fas.fa-check-circle.tcp
        .h4.tcp.mb-0 Estructura de ingresos

    p.mb-5(data-aos="fade-right") Para establecer los flujos de efectivo que generar la empresa, primero se ha de fijar el precio del producto o servicio que ofrecer, para ello, es importante resaltar la propuesta de valor, ya que es all en donde se comprende el papel que juega el producto o servicio en la adquisicin del cliente y cunto se est dispuesto a pagar para satisfacer la necesidad.

    .row.justify-content-center.mb-5
      .col-lg-10.tarjeta.BG02.p-4
        p(data-aos="fade-right") La razn para definir los precios radica en conocer si para la empresa es rentable su participacin en el mercado, si el costo de disear y producir un producto es mayor que lo que el cliente est dispuesto a pagar por l o mayor que los ingresos que generar el producto antes de que finalice su ciclo de vida, entonces no tiene sentido comercial seguir adelante con el producto. 
        p(data-aos="fade-right") En el modelo Canvas se fundamenta el efectivo que se forjar en la fijacin de precios dependiendo del mercado, del volumen, la negociacin, la subasta, la licitacin o la gestin del rendimiento, estas variables pueden utilizarse para determinar el precio de un flujo de ingresos y as poder representar en el modelo canvas los ingresos para cada segmento de clientes de la empresa.
        .row.justify-content-center.mb-4
          .col-lg-6.col-10.mb-lg-0.mb-4(data-aos="fade-right")
            img(src='@/assets/curso/tema1/img34.svg' alt="Imagen decorativa")
          .col-lg-6(data-aos="fade-left")
            p Algunas fuentes de ingresos que generan el flujo de efectivo para la empresa son:
            ul.lista-ul--color
              li(style="margin-bottom: 2px" data-aos="fade-left")
                i.fas.fa-chevron-circle-right.tcp
                | La suscripcin. 
              li(style="margin-bottom: 2px" data-aos="fade-left")
                i.fas.fa-chevron-circle-right.tcp
                | La venta de activos.
              li(style="margin-bottom: 2px" data-aos="fade-left")
                i.fas.fa-chevron-circle-right.tcp
                | Las tarifas de uso.
              li(style="margin-bottom: 2px" data-aos="fade-left")
                i.fas.fa-chevron-circle-right.tcp
                | Las licencias a terceros.
              li(style="margin-bottom: 2px" data-aos="fade-left")
                i.fas.fa-chevron-circle-right.tcp
                | Las tarifas de publicidad.
              li(style="margin-bottom: 2px" data-aos="fade-left")
                i.fas.fa-chevron-circle-right.tcp
                | Las comisiones.
              li(style="margin-bottom: 2px" data-aos="fade-left")
                i.fas.fa-chevron-circle-right.tcp
                | Servicios de alquiler, entre otros 
    
    p.text-center.mb-4 El flujo de ingresos de la empresa muestra el efectivo que gana con cada segmento de sus clientes.

    separador
    #t_1_1.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.1 Clasificacin

    p.mb-5(data-aos="fade-right") Las empresas se pueden clasificar de diferentes maneras: segn las condiciones y caractersticas de cada una, entre ellas se pueden mencionar:

    .mb-4.d-flex(data-aos="fade-right")
      .d-flex.align-items-center.pe-4.BGR01
        img(src='@/assets/curso/temas/img01.svg' style="max-width: 30px;")
        h4.col.ps-3.tcp.mb-0 Segn el tipo de modelo de negocio
    
    p.mb-5(data-aos="fade-right") El tipo de modelo de negocio caracteriza una serie de empresas que se apoyan en l con el fin de posicionarse en el mercado, entre ellos se tiene:

    .row.justify-content-center.mb-5
      .col-lg-4.col-8.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema1/img35.png' alt="Imagen decorativa")
      .col-lg-8(data-aos="fade-left")
        ul.lista-ul--color.mb-4
          li(style="margin-bottom: 2px" data-aos="fade-right")
            i.fas.fa-check-circle.tcp
            .h4.tcp.mb-0 Modelo de franquicia
        p Es el mejor modelo para la expansin de la empresa, la franquicia permite al franquiciador licenciar sus recursos y el nombre de la marca, utilizar la propiedad intelectual y los derechos de una franquicia para vender los productos y servicios a cambio de regalas, unos ejemplos clsicos son: Subway, McDonald's, Coca-Cola.
        p.mb-0 La franquicia es la manera privilegiada de expandirse para la mayora de las marcas pequeas y grandes, porque ya se ha demostrado que es una excelente forma de crecer. Hay tres tipos de modelos, los cuales se describen a continuacin:

    .tarjeta--container.row.mb-5
      .col-lg.tarjeta.color-primario.p-5.text-white
        .row.justify-content-center.mb-4(data-aos="fade-right")
          .col-6
            img(src='@/assets/curso/tema1/img36.svg' alt="Imagen decorativa")
        .h4.text-center Franquicia de formato empresarial
        p.mb-0 Acuerdo de franquicia en el que permite que un tercero haga negocios usando sus marcas registradas y modelo comercial a cambio de tarifas y un porcentaje recurrente de los ingresos por ventas. Los franquiciados bajo este modelo se manejan de acuerdo con las pautas y reglas de la empresa matriz.
      .col-lg.tarjeta.color-secundario.p-5.text-white(data-aos="flip-up")
        .row.justify-content-center.mb-4
          .col-6
            img(src='@/assets/curso/tema1/img37.svg' alt="Imagen decorativa")
        .h4.text-center Franquicia de producto
        p.mb-0 Es el mtodo ms antiguo de franquicia, aqu un franquiciado tiene derecho a distribuir los productos suministrados por el fabricante, por lo que este debe realizar pagos por el uso de los productos de marca registrada del franquiciador o fabricante. Se utiliza popularmente en las industrias manufacturera y automotriz, y es manejado por marcas como: Samsung y Apple.
      .col-lg.tarjeta.color-acento-contenido.p-5(data-aos="fade-left")
        .row.justify-content-center.mb-4
          .col-6
            img(src='@/assets/curso/tema1/img38.svg' alt="Imagen decorativa")
        .h4.text-center Franquicia de fabricacin
        p.mb-0 Generalmente, el franquiciador es una empresa fabricante que otorga derecho exclusivo al franquiciado o al distribuidor para fabricar y distribuir sus productos en un rea bien definida, utilizando el nombre y la marca comercial del franquiciador.

    ul.lista-ul--color.mb-5
      li(style="margin-bottom: 2px" data-aos="fade-right")
        i.fas.fa-check-circle.tcp
        .h4.tcp.mb-0 Modelo de negocio de suscripcin
    
    p(data-aos="fade-right") Este modelo permite que el cliente obtenga servicios pagando una cantidad fija cada mes o ao, en este caso, la empresa tiene que proporcionar suficiente valor a su cliente para que visite el sitio web una y otra vez. Tambin permite a las empresas segmentar el mercado y ofrecer un nmero especfico de artculos en su contenido bajo diferentes planes y precios conocidos como ofertas escalonadas. Ejemplos: Netflix, LinkedIn, Amazon Prime.
    p.mb-5(data-aos="fade-right") En este modelo, el cliente tiene la opcin de renovar o cancelar la suscripcin una vez finalice el contrato, por lo que acepta pagar por un producto o servicio por un perodo de tiempo y la empresa cumple con esta oferta siempre que el cliente complete sus pagos recurrentes. Una suscripcin es una especie de contrato entre la empresa y el cliente. A continuacin, se explican algunos modelos de negocio:

    TabsB.color-acento-botones.mb-5(data-aos="zoom-in")
      .BG07.p-4(titulo="Caja de suscripcin" :icono="require('@/assets/curso/tema1/img39.svg')")
        p.mt-4.mb-0 Cada mes, los clientes reciben una caja llena de varios productos que a veces estn relacionados entre s y a veces no, dicho en otras palabras, algunas empresas otorgan el beneficio de que el cliente elija lo que hay en la caja, mientras que otras obtienen el elemento sorpresa guiados por la experimentacin de nuevos productos que quizs no examinaran por s mismos. Aqu los clientes estn dispuestos a pagar ms por la facilidad de recibir estos artculos en la puerta de su casa. 
      .BG07.p-4(titulo="Modelo de suscripcin al servicio de transmisin" :icono="require('@/assets/curso/tema1/img40.svg')")
        p.mt-4.mb-0 Los servicios de transmisin les otorgan acceso a cosas, como: pelculas, televisin y msica, por un precio mensual. El beneficio de este modelo es que los consumidores pueden acceder a todo el contenido de entretenimiento que necesitan cundo y dnde quieran.
      .BG07.p-4(titulo="Modelo de suscripcin de servicio de alimentos" :icono="require('@/assets/curso/tema1/img41.svg')")
        p.mt-4.mb-0 Se basa en la conveniencia, estas compaas no solo entregan comida a su puerta, sino que tambin le brindan recetas para cocinar. De esa manera, obtendr los ingredientes y la direccin necesarios para preparar una comida casera, incluso si no tiene experiencia en la cocina.
      .BG07.p-4(titulo="Modelo de suscripcin de salud y bienestar" :icono="require('@/assets/curso/tema1/img42.svg')")
        p.mt-4.mb-0 Ofrecen una suscripcin de salud y bienestar a travs del acceso a clases, entrenadores y equipos de entrenamiento por una tarifa mensual.
      .BG07.p-4(titulo="Modelo de suscripcin de <i><i>software</i></i>" :icono="require('@/assets/curso/tema1/img43.svg')")
        p.mt-4.mb-0 Muchas empresas de <i>software</i> utilizan modelos de suscripcin porque suelen actualizar y mejorar sus productos y servicios con regularidad, por lo que en lugar de pedir a los clientes que vuelvan a comprar el producto cada vez que se realiza un cambio, utilizan un modelo de suscripcin de <i>software</i> que les permite realizar mejoras sin obstaculizar la experiencia del cliente.

    ul.lista-ul--color.mb-5
      li(style="margin-bottom: 2px" data-aos="fade-right")
        i.fas.fa-check-circle.tcp
        .h4.tcp.mb-0 Modelo de negocio de venta directa
    
    p(data-aos="fade-right") En este modelo, los productos se venden directamente a los clientes finales, ya sea en una conversacin individual o en una pequea reunin y el vendedor obtiene una parte de cada venta. Aunque la tecnologa ha superado el mtodo de venta directa en muchos sentidos, todava muchas empresas prefieren dar un toque personal a sus clientes. Ejemplos: Herbalife y Avon.
    p.mb-5(data-aos="fade-right") La venta directa es muy popular en diferentes partes del mundo, puesto que enfatiza en los mtodos de venta y la manera de invertir en la mejora de habilidades de venta. Este modelo de negocio tiene ciertos tipos y mtodos de venta, los ms destacados son:

    .row.justify-content-center.mb-5
      .col-lg-10
        .titulo-sexto.color-acento-contenido(data-aos="fade-right")
          h5 Tabla 1.
          i Modelo de negocios
        .tabla-a.color-acento-contenido
          table(data-aos="zoom-in"  alt="Texto alternativo tabla")
            thead(style="border-color: transparent")
              tr(data-aos="fade-right")
                th(style="background-color: #6DEBFF; font-size: 18px;") Tipos de ventas
                th(style="background-color: #6DEBFF; font-size: 18px;") Mtodos de ventas
            tbody
              tr(data-aos="fade-right")
                td #[b Un solo nivel] <br><br>
                  p.mb-0 Un vendedor gana una comisin por distribuir productos. Un caso muy referenciado es de las agencias de publicidad, en las cuales el empleado obtiene cierta comisin de la suma de los servicios que vendi.
                td #[b Ventas en lnea] <br><br>
                  p.mb-0 Las empresas de ventas de un solo nivel y de varios niveles adoran este mtodo. Pequeas y grandes empresas venden productos y servicios a travs de sus propios sitios web o cuentas de redes sociales.
              tr(data-aos="fade-right")
                td(style="background-color: #F6F6F6;") #[b Multinivel] <br><br>
                  p.mb-0 Un representante no solo vende productos, sino que tambin reclutas nuevos vendedores, pues de esta manera obtiene una retribucin tanto por sus propias ventas, como por las realizadas por las personas que han reclutado.
                td(style="background-color: #F6F6F6;") #[b Ventas de anfitriones] <br><br>
                  p.mb-0 Consiste en celebrar una fiesta o reunin entre un grupo de clientes y presentarles un producto. Los participantes disfrutan de un ambiente relajado de celebracin y se inclinan cada vez ms a comprar algo, tambin puede llevarse la presentacin en el sitio de oficina u otro espacio.

    ul.lista-ul--color.mb-5
      li(style="margin-bottom: 2px" data-aos="fade-right")
        i.fas.fa-check-circle.tcp
        .h4.tcp.mb-0 Modelo de negocio de <i><i>marketing</i></i> de afiliados

    p(data-aos="fade-right") En este modelo, las empresas ganan dinero presentando, reseando y recomendando los productos o servicios de otras empresas, por ejemplo: los sitios web de reseas de productos. Estos se pagan en funcin de las oportunidades de venta que brindan a sus empresas proveedoras.
    p.mb-5(data-aos="fade-right") El <i><i>marketing</i></i> de afiliacin puede atraer a muchas personas diferentes en comparacin con otros modelos comerciales, es quizs una de las formas ms diversas de ganar dinero en lnea, ya que existen diferentes tipos de vendedores afiliados, ya que nunca se sabe si la persona realmente us el producto alguna vez o si solo lo est promocionando por el dinero. Se relacionan a continuacin los siguientes tipos de <i><i>marketing</i></i> de afiliacin:

    figure.mb-5(data-aos="zoom-in")
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/1V3tMKCY2Bc?si=twZg0wMaEnHHoCVM" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)

    ul.lista-ul--color.mb-5
      li(style="margin-bottom: 2px" data-aos="fade-right")
        i.fas.fa-check-circle.tcp
        .h4.tcp.mb-0 Modelo de negocio de consultora
    
    p.mb-5(data-aos="fade-right") Las empresas que brindan servicios de consultora mediante la contratacin de personas calificadas y con experiencia a quienes se les asignan los proyectos del cliente. Estas empresas tienden a cobrar por hora o toman un porcentaje de participacin en funcin de la finalizacin exitosa del proyecto. Ejemplo: empresas de desarrollo de <i><i>software</i></i> o sitios web.

    .row.justify-content-center.mb-5
      .col-lg-8
        .cajon.color-primario.p-4(data-aos="flip-up")
          p.mb-0 Los consultores buscan crear estrategias a travs de modelos de negocios innovadores y recientes, por ello, se describen aqu algunos de estos modelos de negocios:

    .tarjeta--container.row.mb-5
      .col-md.tarjeta.color-primario.p-5
        .row.justify-content-center.mb-4
          .col-6
            figure
              img(src='@/assets/curso/tema1/img104.svg', alt='Texto que describa la imagen')
          
        h4.text-center Consultora colaborativa
        p Los modelos de consultora de este tipo tienen <b>costos fijos bajos</b>, ya que los miembros pueden acceder al talento que no est en su nmina a medida que surge un proyecto relevante, aunque esto a su vez <b>puede conducir a costos variables altos</b>, lo que hace que la planificacin financiera a largo plazo sea ms difcil.
        p Las consultoras que utilizan este modelo lo hacen por su capacidad de obtener un conjunto ms amplio de <b>conocimientos y una mayor agilidad para hacer frente a los cambios en la demanda</b>.

      .col-md.tarjeta.color-secundario.p-5
        .row.justify-content-center.mb-4
          .col-6
            figure
              img(src='@/assets/curso/tema1/img104.svg', alt='Texto que describa la imagen')
          
        h4.text-center Consultora continua
        p Aqu las empresas de consultora adoptan un modelo continuo, por lo que pueden contar con un <b>flujo permanente de ingresos de sus clientes</b>, casi como una suscripcin de consultora, lo que lleva a un <b>alto nivel de interaccin en lnea entre el cliente y la empresa</b>.
        p Las herramientas y los procesos implementados por <b>consultores continuos atraen a los clientes por su uso repetido</b>, porque facilitan la entrega de resultados, ayudan a <b>solucionar los problemas</b> que ocurren con regularidad, y pueden estar ms <b>actualizados</b>.

      .col-md.tarjeta.color-acento-contenido.p-5
        .row.justify-content-center.mb-4
          .col-6
            figure
              img(src='@/assets/curso/tema1/img104.svg', alt='Texto que describa la imagen')
          
        h4.text-center Consultora instantnea
        p El modelo de consultora instantnea puede <b>ensear</b> a los empleados de una empresa los <b>conceptos bsicos</b> de los principios financieros antes de aplicar lo aprendido para que las ganancias se aprecien muy rpidamente Busca mostrar su valor al cliente desde la primera oportunidad y es ideal para un entorno <b>empresarial disruptivo</b>, donde la agilidad es clave para mantener el ritmo de los competidores, la fortaleza clave de la consultora instantnea es su <b>velocidad</b>.

    //-figure.mb-5(data-aos="zoom-in")
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/2L91WMqw96A" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)

    ul.lista-ul--color.mb-5
      li(style="margin-bottom: 2px" data-aos="fade-right")
        i.fas.fa-check-circle.tcp
        .h4.tcp.mb-0 Modelo de negocio basado en agencias
    
    p(data-aos="fade-right") Este es un modelo de negocio basado en proyectos, donde se contrata a una empresa externa para completar una tarea especfica. Tradicionalmente, las organizaciones que carecen de experiencia interna contratan agencias para obtener una solucin personalizada para sus necesidades. Ejemplo: McDonald's.
    p.mb-5(data-aos="fade-right") Sin duda, este modelo es vital, ya que ayuda a las empresas a ahorrar costos en la contratacin de expertos para realizar su trabajo. Las empresas pueden acceder al soporte de expertos que mejoran sus marcas, el modelo presenta los beneficios:

    .BG08.p-4.mb-5(data-aos="zoom-in")
      SlyderB(:datos="datosSlyder")

    ul.lista-ul--color.mb-5
      li(style="margin-bottom: 2px" data-aos="fade-right")
        i.fas.fa-check-circle.tcp
        .h4.tcp.mb-0 Modelo de negocio educativo en lnea
    
    p(data-aos="fade-right") Dirigido a la industria educativa, incluidos estudiantes y profesores, en este modelo de negocio se obtiene acceso a recursos educativos a travs de tarifas planas de cursos o suscripcin. Se puede decir que es como una combinacin de tarifas de cursos y un modelo basado en suscripcin. Ejemplos: LinkedIn, Photoshop, <i><i>software</i></i> de Adobe.
    p.mb-5(data-aos="fade-right") Los modelos de negocios de aprendizaje en lnea ms comunes y exitosos son:

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-9.order-lg-1.order-2(data-aos="fade-right")
        TabsA.color-acento-botones
          .tarjeta.BG07.p-4(titulo="Modalidad nocturna")
            p Es un modelo comercial en el que un alumno paga una tarifa nica para acceder al contenido del curso, el cual sigue un marco fijo que incluye mdulos de aprendizaje, evaluaciones, cuestionarios o exmenes para evaluar al alumno.
            p.mb-0 El modelo de escuela nocturna es ms adecuado para los estudiantes que buscan una habilidad especfica o que estn aprendiendo una materia o un idioma en particular. Puede ser til para las nuevas empresas que buscan generar flujo de efectivo mediante las suscripciones y la venta de cursos.
          .tarjeta.BG07.p-4(titulo="Acadmico")
            p Este modelo es por suscripcin, donde la plataforma de educacin en lnea funciona como una escuela virtual motivando a los estudiantes a aprender y desarrollar mltiples habilidades por lo que les proporciona una amplia biblioteca de cursos, videos, sesiones de preguntas y respuestas en vivo, grupos de apoyo y sesiones individuales con los instructores y otros materiales de aprendizaje.
            p.mb-0 Cubre temas amplios, los cuales son luego abiertos como cursos en detalle, atrayendo a estudiantes que buscan aprender mltiples habilidades.
          .tarjeta.BG07.p-4(titulo="Combinado")
            p.mb-0 Ofrece tanto ofertas basadas en suscripcin como cursos independientes con tarifas nicas, es adecuado para aquellos estudiantes que estn dispuestos a pagar ms por material de curso adicional o cursos que puedan ayudarlos a mejorar sus habilidades acadmicas.
      .col-lg-3.col-6.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/tema1/img46.svg' alt="Imagen decorativa")

    ul.lista-ul--color.mb-5
      li(style="margin-bottom: 2px" data-aos="fade-right")
        i.fas.fa-check-circle.tcp
        .h4.tcp.mb-0 Modelo de negocio multimarca
      
    p(data-aos="fade-right") Este modelo se basa en la comercializacin de ms de dos productos, casi similares pero que compiten entre s y estn bajo una sola organizacin, pero con diferentes marcas. Se hace para crear economas de escala y construir un imperio. Ejemplos: Nestl, Unilever y Procter & Gamble.
    p.mb-5(data-aos="fade-right") Este tipo de negocio es una buena idea para las organizaciones que buscan tener presencia en diversos campos, puesto que les ayuda a maximizar las ganancias, conozca las ventajas:

    .titulo-sexto.color-acento-contenido.mb-5(data-aos="fade-right")
      h5 Figura 2.
      span <i>Modelos de negocios de aprendizaje</i>
    figure.mb-5(data-aos="zoom-in")
      img.d-lg-flex.d-none(src='@/assets/curso/tema1/img47.svg' alt="Imagen que relaciona los nombres de los modelos de negocios de aprendizaje en lnea ms comunes.")
      img.d-lg-none.d-flex(src='@/assets/curso/tema1/img47a.svg' alt="Imagen que relaciona los nombres de los modelos de negocios de aprendizaje en lnea ms comunes.")
    
    ul.lista-ul--color.mb-5
      li(style="margin-bottom: 2px" data-aos="fade-right")
        i.fas.fa-check-circle.tcp
        .h4.tcp.mb-0 Modelo de negocio de comercio electrnico
    
    p(data-aos="fade-right") Un modelo comercial simple pero muy prometedor, el comercio electrnico permite a los compradores y vendedores conectarse y realizar transacciones utilizando una plataforma en lnea (tienda en lnea). Ejemplo: Amazon, OLX, Walmart, etc.
    p.mb-5(data-aos="fade-right") Este modelo de negocio permite a las empresas y los consumidores realizar compras o vender cosas en lnea, a continuacin, se muestran algunas alternativas: 

    TabsB.color-acento-botones.mb-5(data-aos="zoom-in")
      .BG07.p-4(titulo="Empresa a Consumidor" :icono="require('@/assets/curso/tema1/img107.svg')")
        p.mt-4 Es cuando una empresa comercializa sus productos o servicios directamente a los usuarios finales.
        p Es la forma de comercio ms conocida, es bastante sencilla y sucede cada vez que compras alimentos en una tienda, se alimenta en un restaurante, va al cine a ver una pelcula, etc.
        p En cualquiera de los anteriores casos, est presente el usuario final de los productos y servicios que venden estas empresas.
      .BG07.p-4(titulo="Empresa a Empresa" :icono="require('@/assets/curso/tema1/img108.svg')")
        p.mt-4 Es cuando una empresa comercializa sus productos o servicios directamente a otras empresas. 
        p Este modelo se puede dividir en dos mtodos: vertical y horizontal. Las empresas orientadas verticalmente venden a clientes dentro de una industria especfica. Las empresas orientadas de manera horizontal, estn vendiendo a clientes en una gran variedad de industrias. 
        p Entre los servicios que ofrece este modelo se encuentra: autenticacin de usuarios, administracin de bases de datos, actualizacin remota, notificaciones automticas, almacenamiento en la nube y alojamiento.
      .BG07.p-4(titulo="Empresa - empresa - consumidor" :icono="require('@/assets/curso/tema1/img109.svg')")
        p.mt-4 Una empresa vende productos a otra empresa que luego los venden a los consumidores.
        p La principal ventaja del modelo de negocio para las empresas de comercio electrnico es el aumento de nuevos clientes. 
        p Esta es una consideracin importante para las nuevas empresas de comercio electrnico que necesitan una forma de hacer crecer rpidamente su base de clientes.
      .BG07.p-4(titulo="Consumidor a Empresa" :icono="require('@/assets/curso/tema1/img110.svg')")
        p.mt-4 Aqu las personas venden bienes y servicios directamente a las empresas.
        p Vemos esto ms comnmente en sitios web que permiten a las personas compartir el trabajo o los servicios en los que son expertos. 
        p A menudo, las empresas presentan una solicitud o una oferta por el tiempo de esa persona y le pagan a travs de esa plataforma. 
        p Este modelo brinda a las empresas la capacidad de encontrar y obtener expertos de apoyo para proyectos especficos.
      .BG07.p-4(titulo="Consumidor a Consumidor" :icono="require('@/assets/curso/tema1/img111.svg')")
        p.mt-4 Los consumidores venden bienes o servicios directamente a otros consumidores.
        p Esto suele ser posible gracias a sitios web de terceros o mercados que facilitan las transacciones en nombre de los compradores y vendedores. 
        p Estos mercados de comercio electrnico permiten a las empresas ms pequeas, o incluso a los aficionados, vender sus productos a su propio precio.
        p Como puede apreciar, el modelo de comercio electrnico ha tomado mucha fuerza gracias a las diferentes estrategias de uso como: sitios web, redes sociales, chat, etc.

    //-figure.mb-5(data-aos="zoom-in")
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/2L91WMqw96A" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)

    ul.lista-ul--color.mb-5
      li(style="margin-bottom: 2px" data-aos="fade-right")
        i.fas.fa-check-circle.tcp
        .h4.tcp.mb-0 Modelos de negocio basados en <i>blockchain</i>
    
    p(data-aos="fade-right") La tecnologa ms avanzada, futurista y moderna de <i>blockchain</i> ha cambiado todo el panorama de las transacciones, involucrando un sistema de red descentralizado a escala global. El uso de una red descentralizada mejora la confianza y permite a los consumidores realizar transacciones entre pares. Las empresas basadas en <i>blockchain</i> obtienen ganancias utilizando <i>tokens</i> y ofrecen <i>blockchain</i> como servicio. Ejemplo: Amazon, Microsoft, Spotify.
    p.mb-5(data-aos="fade-right") Se presentan a continuacin los modelos de negocios basados en <i>blockchain</i>:

    .row.justify-content-center.mb-4
      .col-lg-4.col-8.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema1/img48.png' alt="Imagen decorativa")
      .col-lg-8(data-aos="fade-left")
        LineaTiempoD.color-acento-botones
          p.mb-0(numero="1" titulo="Modelo de negocio de cadena de bloques") La tecnologa <i>blockchain</i> siempre ha sido de igual a igual, lo que permite a los usuarios finales interactuar entre s. El beneficio se puede obtener aqu a travs de #[b <i>tokens</i>], tarifas de transaccin, otros utilizan este modelo de negocio al proporcionar una plataforma para el almacenamiento y el intercambio de datos.
          p.mb-0(numero="2" titulo="<i>Blockchain</i> como modelo de negocio de servicio") La tecnologa y el ecosistema <i>Blockchain</i> pueden ser bastante intimidantes para las personas en general. Sin embargo, este modelo de negocio permite a los clientes #[b subcontratar toda la estructura y enfocarse solo en la interfaz].<br><br>Entre los servicios que ofrece este modelo se encuentra: autenticacin de usuarios, administracin de bases de datos, actualizacin remota, notificaciones automticas, almacenamiento en la nube y alojamiento.
          p.mb-0(numero="3" titulo="Modelo de negocio de fichas de utilidad") El modelo comercial de <i>tokens</i> de utilidad impulsa la funcionalidad en el negocio a travs del uso de <i>tokens</i>, que facilitan las actividades de la red. Hoy en da, hay muchas nuevas empresas y sitios web de comercio electrnico que utilizan el modelo comercial, el cual tiene tres propiedades importantes: funcin, caracterstica y propsito.
          p.mb-0(numero="4" titulo="Productos de <i>software</i> basados en <i>blockchain</i>") Hay empresas de <i>blockchain</i> que crean soluciones para vender a organizaciones ms grandes, a las cuales tambin les dan soporte despus de la implementacin. Es mucho ms fcil comprar una solucin <i>blockchain</i> lista para usar, ya que esto puede ser extremadamente rentable para las empresas que estn a la vanguardia en el uso de tecnologa.
          p.mb-0(numero="5" titulo="Plataformas de desarrollo") Para entender la relacin entre el desarrollo y el valor de la red, en palabras simples, cuantas ms personas estn involucradas, ms valiosa ser la red. Por lo tanto, muchos estn lanzando y creando aplicaciones descentralizadas en plataformas de desarrollo a travs de la tecnologa y el ecosistema de <i>blockchain</i>.

    separador
    #t_1_2.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.2 Etapas
    
    p(data-aos="fade-right") Las etapas expresan directamente el negocio como un todo, dando a conocer las diferentes fases por las que atraviesa el modelo desde sus comienzos hasta su final, adems de analizar los requisitos que se han de tener en cuenta para cada ciclo.
    p.mb-4(data-aos="fade-right") Cada etapa del modelo necesita de la formulacin de estratgicas y tcticas que sean lo suficientemente completas para liderarlo adecuadamente. La importancia de cada ciclo radica en los lineamientos y pautas, ya que las empresas que naufragaron lo hicieron en gran parte por no conectar el proceso a los lineamientos, para conocer las etapas le invito a revisar el siguiente contenido:

    figure.mb-5(data-aos="zoom-in")
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/mYgnm1SlUbY?si=c7gTkmcJaDDTOw4m" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)

    .row.justify-content-center.mb-4
      .col-lg-8
        .cajon.color-primario.p-4(data-aos="flip-up")
          p.mb-0 Cada etapa del modelo de negocio contribuye considerablemente a los esfuerzos de xito o fracaso del ejercicio en los que se impacta de manera directa los periodos de la empresa.  Se sugiere atender a los lineamientos de cada fase, tomarse la tarea de analizar y comprender las necesidades para as tomar las decisiones acertadas.

    separador
    #t_1_3.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.3 Marcos de referencia
    
    p.mb-5(data-aos="fade-right") Se describe como un conjunto especfico de ideas en las que alguien puede basar su juicio.  Tambin se le conoce como suposicin que determina un comportamiento tpico. Tiene un impacto directo en el posicionamiento de los productos y servicios de una marca.

    .bloque-texto-g.color-primario.p-3.p-sm-4.p-md-5.mb-5
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/tema1/img49.jpg')})`}" data-aos="fade-right"
      )
      .bloque-texto-g__texto.p-4(data-aos="fade-left")
        p El marco de referencia se define como un proceso que permite comparar un producto  o servicio con otras ofertas de marcas rivales. Por lo general, los interesados potenciales la realizan para conocer el mercado objetivo de una empresa especfica.<br><br>
          | Estos se utilizan en un contexto general en el que se sita, infiere o examina una situacin. Si se dice que pertenece a un marco demasiado estrecho, significa que se dejan de lado algunos factores esenciales, mientras que un marco demasiado amplio significa factores que pueden resultar irrelevantes para el contexto o simplemente distracciones. El hecho es que se considera una herramienta esencial para cambiar las mentes en funcin de nuestra comprensin de la situacin.

    p(data-aos="fade-right") En los negocios, los marcos de referencia son una estructura de puntos de vista, valores y costumbres a travs de los cuales un individuo o grupo puede percibir, identificar, evaluar informacin y datos, puede comunicar ideas, conceptos y monitorear el comportamiento. Se utiliza para describir la distribucin consciente o atenta de un producto o servicio en el mercado. El <i>marketing</i> comienza cuando se crea una referencia, porque al principio, es la forma en que el consumidor en el mercado percibe el servicio o producto.
    p(data-aos="fade-right") Tambin es un concepto que asegura cmo el pblico objetivo ve un producto o servicio en el mercado. Su idea bsica es crear una imagen particular, una vista u opinin sobre el servicio, producto o concepto que comercializan las entidades comerciales. 
    p.mb-5(data-aos="fade-right") Es por eso que los marcos de referencia tambin pueden describirse como la concepcin de una accin, el resultado y las contingencias que estn vinculadas con una eleccin especfica.

    .row.justify-content-center.mb-5
      .col-lg-10
        .tarjeta.color-secundario.p-4.text-white
          .row.justify-content-center.align-items-center
            .col-lg-2.col-4.mb-lg-0.mb-4(data-aos="fade-right")
              img.mx-auto(src='@/assets/curso/tema1/img01.svg' alt="Imagen decorativa" style="max-width: 90px;")
            .col-lg-10(data-aos="fade-left")
              .h4 Importante
              p.mb-0 El nombre de la marca a menudo se convierte en un marco de referencia para los productos o servicios lanzados por la marca. Ejemplo: #[b SAMSUNG], una marca mundial, lanz un nuevo producto en el mercado de consumo, ya est establecido, ya que la gente lo comprar con los otros productos lanzados por la marca.

    p.text-center.mb-5(data-aos="fade-right") En el siguiente contenido se exploran las ventajas del marco de referencia en un modelo de negocios:

    SlyderF.mb-5(columnas="col-lg-6 col-xl-4" data-aos="zoom-in")
      .tarjeta.color-primario.h-100.p-4
        .row.justify-content-center.mb-4
          .col-8
            img(src='@/assets/curso/tema1/img50.svg' alt='Imagen decorativa')
        .text-white
          .h4 Precisa la propuesta de valor
          p.mb-0 La empresa puede persuadir a las personas a travs de este proceso de que su producto o servicio es el mejor del mercado y que resultar favorable para el cliente usarlo.
      .tarjeta.color-primario.h-100.p-4
        .row.justify-content-center.mb-4
          .col-8
            img(src='@/assets/curso/tema1/img51.svg' alt='Imagen decorativa')
        .text-white
          .h4 Puntos de partida
          p.mb-0 Resulta beneficioso saber cmo su producto o servicio es superior a los que ofrecen las marcas rivales en el mercado. Proporciona una razn slida para que el consumidor compre ese artculo, partiendo de la comparacin que realice.
      .tarjeta.color-primario.h-100.p-4
        .row.justify-content-center.mb-4
          .col-8
            img(src='@/assets/curso/tema1/img52.svg' alt='Imagen decorativa')
        .text-white
          .h4 Puntos de diferencias 
          p.mb-0 Si el producto tiene alguna caracterstica innovadora que no se ofrece en otros productos en el mercado de consumo, entonces este punto puede marcar una diferencia formidable en la forma en que el consumidor ve su producto o servicio.
      .tarjeta.color-primario.h-100.p-4
        .row.justify-content-center.mb-4
          .col-8
            img(src='@/assets/curso/tema1/img53.svg' alt='Imagen decorativa')
        .text-white
          .h4 Comparaciones directas
          p.mb-0 Toma varios productos o servicios y crea referencias para hacer comparaciones utilizando productos o servicios existentes, el proceso tambin ayuda a confrontar artculos nuevos con marcas establecidas para darles una fuerte ventaja competitiva.
      .tarjeta.color-primario.h-100.p-4
        .row.justify-content-center.mb-4
          .col-8
            img(src='@/assets/curso/tema1/img54.svg' alt='Imagen decorativa')
        .text-white
          .h4 Posicionamiento de la marca
          p.mb-0 Cuando un cliente comienza a identificarse con una marca debido al marco de referencia, se alinea con el producto y se utiliza para construir una imagen de marca positiva en el mercado.

    .mb-4.d-flex(data-aos="fade-right")
      .d-flex.align-items-center.pe-4.BGR01
        img(src='@/assets/curso/temas/img01.svg' style="max-width: 30px;")
        h4.col.ps-3.tcp.mb-0 Clasificacin de los marcos de referencia
    
    p.mb-5(data-aos="fade-right") El marco de referencia ofrece a la mayora de las organizaciones una fuente confiable de informacin que ayuda a gestionar un negocio ante los desafos que enfrenta en el mercado, es decir, la estructura se usa para comprender y asegurar que las labores se realizan de manera eficiente y se monitorean constantemente. A continuacin, se describen cada uno de los marcos que apoyan a la organizacin desde diferentes mbitos en la preparacin hacia la estabilidad y posicionamiento del mercado:

    LineaTiempoE.color-acento-contenido.mb-5
      .row.justify-content-center(titulo="01")
        .col-lg-2.col-4.mb-lg-0.mb-4
          img(src='@/assets/curso/tema1/img55.svg' alt="Imagen decorativa")
        .col-lg-10
          .h4 Marco comercial
          p.mb-0 Ofrecen las herramientas para analizar problemas del mbito comercial y sugerir cambios, por lo que las expectativas de los clientes aumentan continuamente, lo que se debe a los cambios en el panorama empresarial provocados por los avances tcnicos.
      .row.justify-content-center(titulo="02")
        .col-lg-2.col-4.mb-lg-0.mb-4
          img(src='@/assets/curso/tema1/img56.svg' alt="Imagen decorativa")
        .col-lg-10
          .h4 Marco empresarial
          p.mb-0 Simplifica los problemas empresariales difciles de solucionar a travs de una estructura, ya que ampla la identidad y las capacidades para cumplir los objetivos generales de la empresa utilizando herramientas de anlisis.
      .row.justify-content-center(titulo="03")
        .col-lg-2.col-4.mb-lg-0.mb-4
          img(src='@/assets/curso/tema1/img57.svg' alt="Imagen decorativa")
        .col-lg-10
          .h4 Marco de producto
          p.mb-0 Necesita un marco de referencia para que sus clientes potenciales puedan comparar su producto con otros de la misma categora y que ofrecen beneficios similares. Utiliza instrumentos como el modelo Aida y el embudo de mercadeo.
      .row.justify-content-center(titulo="04")
        .col-lg-2.col-4.mb-lg-0.mb-4
          img(src='@/assets/curso/tema1/img58.svg' alt="Imagen decorativa")
        .col-lg-10
          .h4 Marco de gestin
          p.mb-0 El establecimiento de herramientas para asignar diferentes tipos de estilos de gestin para contribuir a las decisiones y labores de los gerentes, usa estos marcos para ayudar a optimizar su proceso y reforzar el pensamiento estratgico.

    .BGM09.px-md-5.px-4.mb-5
      .mb-4.d-flex(data-aos="fade-right")
        .d-flex.align-items-center.pe-4.BGR01
          img(src='@/assets/curso/temas/img01.svg' style="max-width: 30px;")
          h4.col.ps-3.tcp.mb-0 Tcnicas de anlisis
    
      p(data-aos="fade-right") Las tcnicas de anlisis enfocadas en los marcos de referencia auxilian a las empresas en la comprensin de los elementos para el crecimiento del negocio, analizando los procesos internos, las polticas, el contexto externo y los activos de la informacin para lograr los objetivos de la empresa. 
      p.mb-5(data-aos="fade-right") Con la implementacin de estos modelos se brindan conocimientos e informacin necesaria para responder a los riesgos y soluciones empresariales, por lo que se utilizan diversidad de tcnicas:

      .row.justify-content-center.mb-5
        .col-lg-10
          ul.lista-ul--color.mb-5
            li(style="margin-bottom: 2px" data-aos="fade-right")
              i.fas.fa-check-circle.tcp
              .h4.tcp.mb-0 Anlisis PESTEL
          p(data-aos="fade-right") Es un marco o herramienta utilizada por los especialistas en <i>marketing</i> para analizar y monitorear el entorno de <i>marketing</i> externo que tienen un impacto en la empresa, el resultado se utiliza para identificar amenazas y debilidades en un anlisis FODA. Esta tcnica se utiliza en las primeras fases del desarrollo de la estrategia para describir el panorama y el entorno en el que opera una organizacin. 
          p(data-aos="fade-right") Esta herramienta es especialmente til cuando se inicia un nuevo negocio o se ingresa a un mercado extranjero. A menudo se utiliza en colaboracin con otras herramientas analticas de negocios, como el anlisis DAFO_FODA y las Cinco Fuerzas de Porter, para brindar una comprensin clara de una situacin y los factores internos y externos relacionados.
          p.mb-5(data-aos="fade-right") El trmino #[b Pestel] contempla los siguientes elementos:
          .row.justify-content-center
