            figure.movil(data-aos="zoom-in")
              img(src='@/assets/curso/tema1/img59_.svg' alt="Imagen que relaciona lo que representa cada letra del trmino PESTEL.")

      ul.lista-ul--color.mb-5
        li(style="margin-bottom: 2px" data-aos="fade-right")
          i.fas.fa-check-circle.tcp
          .h4.tcp.mb-0 Las cinco fuerzas de Porter
      
      p.mb-5(data-aos="fade-right") Ayuda a analizar el grado de competencia dentro de una determinada industria. Es especialmente til al iniciar un nuevo negocio o al ingresar a un nuevo sector industrial. Segn este marco, la competitividad no proviene slo de los competidores, el estado de competencia en una industria depende de cinco fuerzas bsicas:

      .row.justify-content-center.mb-5
        .col-lg-10
          ul.lista-ul--color
            li(style="margin-bottom: 2px" data-aos="fade-right")
              i.fas.fa-chevron-circle-right.tcp
              | Amenaza de nuevos participantes.
            li(style="margin-bottom: 2px" data-aos="fade-right")
              i.fas.fa-chevron-circle-right.tcp
