            li(style="margin-bottom: 2px" data-aos="fade-right")
              i.fas.fa-chevron-circle-right.tcp
              | Amenaza de productos o servicios sustitutos.
            li(style="margin-bottom: 2px" data-aos="fade-right")
              i.fas.fa-chevron-circle-right.tcp
              | Rivalidad existente en la industria. 

          .row.justify-content-center
            .col-lg-10
              .tarjeta.color-secundario.p-4.text-white
                .row.justify-content-center.align-items-center
                  .col-lg-2.col-4.mb-lg-0.mb-4(data-aos="fade-right")
                    img.mx-auto(src='@/assets/curso/tema1/img01.svg' alt="Imagen decorativa" style="max-width: 90px;")
                  .col-lg-10(data-aos="fade-left")
                    .h4 Importante
                    p.mb-0 Es una herramienta poderosa que se utiliza para establecer las principales competencias en el entorno, comprender las fuerzas que sensibilizan el mercado, aumentar la rentabilidad y mantenerse siempre un paso delante de la competencia, ayuda a ajustar las estrategias en caso de ser necesario y finalmente evita dar pasos en falso aprovechando la fuerte posicin.

    .row.justify-content-center.mb-5
      .col-lg-10(data-aos="fade-right")
        p El anlisis de las fortalezas y debilidades de la empresa tambin se convierte en otras de las ventajas que ofrece el anlisis de Porter, ya que ayuda a identificar los factores crticos que le afectan.
        p.mb-0 Las cinco fuerzas de Porter son:

    .row.justify-content-center.mb-5
      .col-lg-10.mb-4
