    p.mb-5(data-aos="fade-right") Estas cinco fuerzas sustentan la estrategia, le ayudan a analizar los intereses e inclinaciones de la industria, le permiten evaluar las opciones de inversin y medir la competencia.

    ul.lista-ul--color.mb-5
      li(style="margin-bottom: 2px" data-aos="fade-right")
        i.fas.fa-check-circle.tcp
        .h4.tcp.mb-0 Matriz de Ansoff
    
    p(data-aos="fade-right") Es un Modelo de anlisis de crecimiento que se compone de cuatro estrategias de desarrollo que puede utilizar para ayudar a fortalecer el negocio, analizar los riesgos asociados a cada uno, resume rpidamente las estrategias y las compara con el riesgo que est asociado a cada una de ellas. Esta herramienta de anlisis es ideal para los especialistas en el <i>marketing</i>, puesto que les ayuda a comprender el crecimiento y el diseo de estrategias motivados por la expansin del negocio. 
    
    .row.justify-content-center.mb-5
      .col-lg-10
        .cajon.color-secundario.p-4.mb-4(data-aos="fade-right")
          p En este modelo se fusiona la penetracin del mercado, el desarrollo de mercado y el desarrollo de productos, que son algunas de las alternativas que la empresa utiliza para alcanzar y aumentar la oferta de los productos. 
          p.mb-0 Cada una de estas estrategias viene con un cierto nivel de riesgo en la implementacin que los lderes de la organizacin pueden evaluar antes de seguir adelante con el uso de la estrategia.
        p.mb-5(data-aos="fade-right") La matriz de Ansoff  est compuesta por los siguientes elementos:
        .titulo-sexto.color-acento-contenido.mb-5(data-aos="fade-right")
          h5 Figura 4.
          span <i>Matriz de Ansoff</i>
        figure
          img.d-lg-flex.d-none(src='@/assets/curso/tema1/img70.svg' alt="Figura que contiene una representacin de la Matriz de Ansoff, la cual relaciona aspectos de mercado y productos como son: desarrollo de mercado, diversificacin, penetracin del mercado y desarrollo de productos.")
          img.d-lg-none.d-flex(src='@/assets/curso/tema1/img70a.svg' alt="Figura que contiene una representacin de la Matriz de Ansoff, la cual relaciona aspectos de mercado y productos como son: desarrollo de mercado, diversificacin, penetracin del mercado y desarrollo de productos.")
    
    ul.lista-ul--color.mb-5
      li(style="margin-bottom: 2px" data-aos="fade-right")
        i.fas.fa-check-circle.tcp
        .h4.tcp.mb-0 Anlisis FODA
    
    p(data-aos="fade-right") Mtodo o herramienta que le permite analizar qu procesos lleva mejor la empresa y analizar los factores internos y externos que la pueden afectar, por lo que se usa en el desarrollo de la planeacin estratgica, ya que permite disear estrategias para el futuro. Identifica las reas del negocio que se encuentran estancadas y le orienta para que las aproveche.
    p.mb-5(data-aos="fade-right") Es una tcnica muy utilizada por la planeacin estratgica, ya que permite diagnosticar las fortalezas, oportunidades, debilidades y amenazas, adems, conduce a un anlisis basado en realidades y no en supuestos. 

    .row.justify-content-center.mb-5
      .col-lg-8
        .titulo-sexto.color-acento-contenido.mb-5(data-aos="fade-right")
          h5 Figura 5.
          span <i>Matriz FODA</i>
        figure
          img.d-lg-flex.d-none(src='@/assets/curso/tema1/img71.svg' alt="Figura que representa los aspectos de la Matriz FODA, los cuales se dividen por: Factores internos: Fortalezas  Debilidades Factores internos: Oportunidades  Amenazas")
          img.d-lg-none.d-flex(src='@/assets/curso/tema1/img71a.svg' alt="Figura que representa los aspectos de la Matriz FODA, los cuales se dividen por: Factores internos: Fortalezas  Debilidades Factores internos: Oportunidades  Amenazas")

    ul.lista-ul--color.mb-5
      li(style="margin-bottom: 2px" data-aos="fade-right")
        i.fas.fa-check-circle.tcp
        .h4.tcp.mb-0 Anlisis de la cadena de valor
    
    p(data-aos="fade-right") A travs de esta tcnica se identifican las actividades principales y de apoyo que generan valor a su producto y reducen costos. Este enfoque analiza e identifica dnde genera mayor valor para el cliente. El objetivo es reconocer las ventajas para la empresa y cules requieren de mejora para introducir una ventaja competitiva. 
    p.mb-5(data-aos="fade-right") Las empresas se amparan en este anlisis porque les brinda la capacidad de producir artculos a bajos costos que los del mercado y ofrecer productos con propuesta de valor para aumentar sus ganancias. Realizar las actividades mucho mejor de lo que las realiza la competencia le garantiza a la organizacin que compita a travs de la ventaja de la divergencia, revise la siguiente tabla que le muestra las actividades que se tienen en cuenta. 

