        i.fas.fa-check-circle.tcp
        .h4.tcp.mb-0 Modelo AIDA
    
    p(data-aos="fade-right") Contribuye en las decisiones publicitarias para los clientes en diferentes etapas del proceso de toma de decisiones. En cada etapa, los conocedores de <i>marketing</i> debern adaptar sus campaas de <i>marketing</i> para ayudar a los clientes a pasar de una etapa a la siguiente, por lo que le ayuda a identificar las cognitivas por las que pasa un consumidor durante el proceso de compra de un producto o servicio. Su objetivo es inducir a los compradores que van y vienen en cada etapa, para ayudarlos a realizar la compra final.
    p.mb-5(data-aos="fade-right") En trminos generales, este modelo atiende las fases por las que atraviesa un cliente en el proceso de compra del producto o la adquisicin del servicio. Los elementos del Modelo o tcnica AIDA se describen a continuacin:

    .row.justify-content-center.mb-4
      .col-xl-3.col-md-6.col-10.mb-4(data-aos="fade-right")
        .tarjeta--boton.color-acento-botones.h-100.p-4
          .row.justify-content-center.align-items-center.mb-5
            .col-6
              .bottomCircle
                figure
                  img.mx-auto(src='@/assets/curso/tema1/img72.svg' style="width: 100px; top: 10px; position: relative" alt="Imagen decorativa")
          .h4.text-center Atencin
          p.mb-0 El producto debe llamar la atencin del consumidor, esta curiosidad se despierta a travs de los materiales publicitarios, los consumidores son muy visuales, utilice ello a su favor para explotar este recurso.
      .col-xl-3.col-md-6.col-10.mb-4(data-aos="flip-right")
        .tarjeta--boton.color-acento-botones.h-100.p-4
          .row.justify-content-center.align-items-center.mb-5
            .col-6
              .bottomCircle
                figure
                  img.mx-auto(src='@/assets/curso/tema1/img73.svg' style="width: 100px; top: 10px; position: relative" alt="Imagen decorativa")
          .h4.text-center Inters
          p.mb-0 Incentivar en el beneficio del producto o servicio, e inters suficiente para animar al comprador a comenzar a necesitar ms.
      .col-xl-3.col-md-6.col-10.mb-4(data-aos="flip-left")
        .tarjeta--boton.color-acento-botones.h-100.p-4
          .row.justify-content-center.align-items-center.mb-5
            .col-6
              .bottomCircle
                figure
                  img.mx-auto(src='@/assets/curso/tema1/img74.svg' style="width: 100px; top: 10px; position: relative" alt="Imagen decorativa")
          .h4.text-center Deseo
          p.mb-0 Despertar el inters por el producto o servicio a travs de emociones y sensaciones, mostrando la personalidad de la marca, esta tarea no recae solo en el vendedor, tambin por un medio publicitario.
      .col-xl-3.col-md-6.col-10.mb-4(data-aos="fade-left")
        .tarjeta--boton.color-acento-botones.h-100.p-4
          .row.justify-content-center.align-items-center.mb-5
            .col-6
              .bottomCircle
                figure
                  img.mx-auto(src='@/assets/curso/tema1/img75.svg' style="width: 100px; top: 10px; position: relative" alt="Imagen decorativa")
          .h4.text-center Accin
          p.mb-0 Llevar al consumidor al siguiente paso que es efectuar la compra, adquirir el servicio, puede hacer la compra en lnea sin salir de casa, realizar una llamada telefnica, o utilizar las redes sociales para concretar la compra de artculo.

    separador
    #t_1_4.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.4 Procesos organizacionales

    p(data-aos="fade-right") Los procesos de la organizacin permiten que los empleados trabajen juntos sin ningn conflicto para alcanzar la meta. Por lo tanto, sin procesos organizacionales claros y efectivos, el negocio correr un riesgo significativo. 
    p(data-aos="fade-right") Los procesos de la organizacin brindan claridad a los empleados al prestar atencin a los diferentes mtodos de cmo realizar las tareas. Como resultado, todos los departamentos dentro de la organizacin pueden trabajar bien juntos como una mquina bien alimentada.
    p.mb-5(data-aos="fade-right") La divisin de los procesos de la organizacin son los que se describen a continuacin:

    .container.row.mb-5
      .col-lg.BG02.p-xl-5.p-4(data-aos="fade-right")
        .row.justify-content-center.mb-4
          .col-lg-4.col-6
            figure
              img(src='@/assets/curso/tema1/img76.svg', alt='Imagen decorativa') 
        h4.text-center Proceso de <i>marketing</i>
        p Es responsable de identificar a los clientes objetivo y definir los deseos y necesidades que la organizacin quiere satisfacer.
      .col-lg.BG05.p-xl-5.p-4(data-aos="flip-right")
        .row.justify-content-center.mb-4
          .col-lg-4.col-6
            figure
              img(src='@/assets/curso/tema1/img77.svg', alt='Imagen decorativa') 
        h4.text-center Proceso de produccin
        p La organizacin utiliza el proceso de produccin para fabricar bienes o servicios para los clientes de acuerdo con las especificaciones establecidas por el proceso de <i>marketing</i>.
      .col-lg.BG02.p-xl-5.p-4(data-aos="flip-left")
        .row.justify-content-center.mb-4
          .col-lg-4.col-6
            figure
              img(src='@/assets/curso/tema1/img78.svg', alt='Imagen decorativa') 
        h4.text-center Proceso de talento humano
        p El proceso de talento humano es responsable de seleccionar, capacitar, evaluar a los empleados de la empresa y cuidar todos sus asuntos para realizar el trabajo requerido de ellos de manera ptima.
      .col-lg.BG05.p-xl-5.p-4(data-aos="fade-left")
        .row.justify-content-center.mb-4
          .col-lg-4.col-6
            figure
              img(src='@/assets/curso/tema1/img79.svg', alt='Imagen decorativa') 
        h4.text-center Proceso financiero
        p Es responsable de determinar el presupuesto requerido para el trabajo de la empresa y de monitorear el gasto de dinero segn sea necesario y especificado.

    p(data-aos="fade-right") Estos procesos representan la principal misin que la organizacin debe cuidar, es por eso que procura alcanzar sus objetivos de manera rpida y eficiente. Si estos no se llevan a cabo de manera adecuada, los resultados podran producir frustracin, confusin, prdida de eficiencia y eficacia limitada.
    p.mb-5(data-aos="fade-right") Hay otros procesos de naturaleza auxiliares que no estn directamente relacionado con la funcin principal de la organizacin, pero asisten y respaldan los llamados principales en una empresa, estos deben cumplir los siguientes pasos: 

    .row.justify-content-center.mb-5
      .col-lg-5.col-10.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema1/img80.png' alt="Imagen decorativa")
      .col-lg-7(data-aos="fade-left")
        LineaTiempoD.color-acento-botones
          p.mb-0(numero="1" titulo="Planes y objetivos") #[b.tcb Examinar planes y objetivos] <br><br>
            | Los objetivos son las actividades especficas que deben completarse para alcanzar las metas. Los planes dan forma a las actividades necesarias para alcanzar esos objetivos.<br><br>
            | La direccin debe examinar los planes inicialmente y continuar hacindolo a medida que los planes cambian y se desarrollan nuevas metas.
          p.mb-0(numero="2" titulo="Actividades") #[b.tcb Establecer actividades para alcanzar los objetivos] <br><br>
            | Se consideran y analizan todas las tareas que deben realizarse para alcanzar las metas de la organizacin.
          p.mb-0(numero="3" titulo="Equipos") #[b.tcb Categorizar y concentrar las actividades de trabajo en equipos manejables, delegar autoridad y asignar actividades] <br><br>
            | Consigue agrupar actividades con base en cuatro modelos de departamento: funcional, espacial, producto y cliente. <br><br>
            | La direccin asigna las actividades de trabajo definidas a trabajadores especficos, adems, otorga a cada trabajador la potestad para llevar a cabo las tareas asignadas.
          p.mb-0(numero="4" titulo="Jerarqua de relaciones") Definir la toma de decisiones y la coordinacin administrativa de la organizacin con base en la jerarqua de relaciones, por lo que aqu es til usar un organigrama.

    .row.justify-content-center.mb-5
      .col-lg-10
        .cajon.color-primario.p-4.mb-4(data-aos="fade-right")
          p.mb-0 Los procesos organizacionales son la secuencia de tareas que, al agregar valor a estas variables y trabajar sobre ellas, generan como resultado final el producto o servicio que ofrecen las empresas. Ahora bien, es importante definir los tipos de procesos existentes dentro de una organizacin:

    .mb-4.d-flex(data-aos="fade-right")
      .d-flex.align-items-center.pe-4.BGR01
        img(src='@/assets/curso/temas/img01.svg' style="max-width: 30px;")
        h4.col.ps-3.tcp.mb-0 Segn su naturaleza
    
    p.mb-5 Toda organizacin cuenta con una serie de procesos que le permiten cumplir con los objetivos propuestos y las actividades segn el cronograma establecido, por esa razn es indispensable conocer cules son:

    .titulo-sexto.color-acento-contenido.mb-5(data-aos="fade-right")
      h5 Figura 6.
      span <i>Proceso segn su naturaleza</i>
    figure.mb-5
      img.mb-lg-0.mb-4(src='@/assets/curso/tema1/img81.svg', alt='Figura que relaciona los aspectos que se deben tener en cuenta en la naturaleza de las organizaciones, encontrando el seguimiento y mejora continua, as como la evaluacin independiente y asesora; de igual manera, se tienen en cuenta diversas necesidades y procesos.')
    a.anexo.d-lg-none.d-flex.movil(:href="obtenerLink('/downloads/anexo_mapa_de_procesos.pdf')" target="_blank")
      .anexo__icono
        img(src="@/assets/template/icono-pdf.svg")
      .anexo__texto
        p #[b Descargar]. El Mapa de procesos.

    .mb-4.d-flex(data-aos="fade-right").mt-5
      .d-flex.align-items-center.pe-4.BGR01
        img(src='@/assets/curso/temas/img01.svg' style="max-width: 30px;")
        h4.col.ps-3.tcp.mb-0 Segn su desempeo
    
    p.mb-5(data-aos="fade-right") Otra forma de identificar los procesos organizacionales tiene que ver directamente con el desempeo en cada una de las reas, este tema se explora a continuacin.

    AcordionA(tipo="b" clase-tarjeta="tarjeta BGR02" data-aos="zoom-in")
      .row.justify-content-center(titulo="Equipos de trabajo funcionales")
        .col-lg-6.order-lg-1.order-2
          p Conformado por miembros de varios niveles verticales de la jerarqua organizacional que realizan funciones especficas. La mayora de las organizaciones que tienen diferentes reas funcionales se organizan en equipos funcionales independientemente del tamao de la empresa. Administrar las operaciones internas y las relaciones externas de un departamento o divisin particular de la organizacin: 
          ul.lista-ul--color
            li(style="margin-bottom: 2px")
              i.fas.fa-chevron-circle-right.tcp
              | Contabilidad. 
            li(style="margin-bottom: 2px")
              i.fas.fa-chevron-circle-right.tcp
              | <i>Marketing</i>.
            li(style="margin-bottom: 2px")
              i.fas.fa-chevron-circle-right.tcp
              | Finanzas. 
            li(style="margin-bottom: 2px")
              i.fas.fa-chevron-circle-right.tcp
              | Recursos humanos.
        .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
          img(src='@/assets/curso/tema1/img82.png' alt="Imagen decorativa")
        .col.order-3
          p.mb-0 Estos son ejemplos de equipos de trabajo funcionales. Los miembros funcionales del equipo suelen tener diferentes responsabilidades, pero todos trabajan para realizar la misma funcin del departamento.
      .row.justify-content-center(titulo="Equipos de trabajo multifuncionales")
        .col-lg-6.order-lg-1.order-2
          p Conformado por expertos de varias reas funcionales y trabajan de manera cooperativa hacia algn objetivo organizacional. Se cree que los equipos multifuncionales mejoran la coordinacin de actividades interdependientes entre subunidades especializadas.
          p Como se ha mencionado anteriormente contribuyen al funcionamiento de las dependencias y por ende al desarrollo de los objetivos de la organizacin, hay que resaltar el establecimiento de los mapas de procesos, ya que en ellos se condensa la informacin que muestra quin y qu est involucrado en un proceso.
        .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
          img(src='@/assets/curso/tema1/img83.png' alt="Imagen decorativa")
        .col.order-3
          p.mb-0 Este tambin puede usarse en cualquier negocio u organizacin y revela reas donde un proceso debe mejorarse, proporcionando una imagen concisa de los pasos necesarios para llevar desde el principio hasta el final la secuencia de pasos que deben llevarse a cabo un proceso, producto o servicio.

    p.mb-5(data-aos="fade-right") Por lo anterior, se hace necesario trabajar el tema de mapa de procesos porque toma gran importancia en la organizacin empresarial, por lo que se ampliar a continuacin.

    ul.lista-ul--color.mb-5
      li(style="margin-bottom: 2px" data-aos="fade-right")
        i.fas.fa-check-circle.tcp
        .h4.tcp.mb-0 Mapa de Procesos
    
    p.mb-5(data-aos="fade-right") Este escribe visualmente el flujo de trabajo, mostrando una serie de eventos que producen un resultado final, tambin se denomina diagrama de flujo, diagrama de flujo de proceso, diagrama de proceso, modelo de proceso o diagrama de flujo de proceso y cumple las siguientes funciones:

    .row.justify-content-center.mb-5
      .col-lg-10
        .BG05.p-4.mb-4
          ul.lista-ul--color
            li(style="margin-bottom: 2px" data-aos="fade-right")
              i.fas.fa-chevron-circle-right.tcb
              | Brinda informacin sobre un proceso, ayuda a los equipos a intercambiar ideas para mejorar el proceso, aumenta la comunicacin y proporciona documentacin del proceso.
            li(style="margin-bottom: 2px" data-aos="fade-right")
              i.fas.fa-chevron-circle-right.tcb
              | Identificar cuellos de botella y retrasos.
            li(style="margin-bottom: 2px" data-aos="fade-right")
              i.fas.fa-chevron-circle-right.tcb
              | Ayuda a definir los lmites del proceso, la propiedad, las responsabilidades y las medidas de eficacia del proceso en el que las organizaciones, con el fin de que las empresas mejoren la eficiencia.
        p(data-aos="fade-right") Aqu se hace necesario conocer cules son los beneficios del mapa de procesos en las organizaciones y por qu son indispensables en los procesos de sistemas de gestin de calidad, veamos el siguiente listado:
        .BG02.p-4.mb-4
          .row.justify-content-center
            .col-lg-4.col-8.mb-lg-0.mb-4
              img.mx-auto(src='@/assets/curso/tema1/img84.svg' style="width: 210px" alt="Imagen decorativa")
            .col-lg-8
              ul.lista-ul--color
                li(style="margin-bottom: 2px" data-aos="fade-left")
                  i.fas.fa-chevron-circle-right.tcp
                  | Inciden en los desperdicios.
                li(style="margin-bottom: 2px" data-aos="fade-left")
                  i.fas.fa-chevron-circle-right.tcp
                  | Agilizan los procesos de trabajo y genera comprensin.
                li(style="margin-bottom: 2px" data-aos="fade-left")
                  i.fas.fa-chevron-circle-right.tcp
                  | Permiten comunicar visualmente los detalles importantes de un proceso.
                li(style="margin-bottom: 2px" data-aos="fade-left")
                  i.fas.fa-chevron-circle-right.tcp
                  | Amplan la comprensin de un proceso.
                li(style="margin-bottom: 2px" data-aos="fade-left")
                  i.fas.fa-chevron-circle-right.tcp
                  | Examinan cmo se podra optimizar un proceso.
                li(style="margin-bottom: 2px" data-aos="fade-left")
                  i.fas.fa-chevron-circle-right.tcp
                  | Mejoran la comunicacin entre personas involucradas en el mismo proceso.
                li(style="margin-bottom: 2px" data-aos="fade-left")
                  i.fas.fa-chevron-circle-right.tcp
                  | Proporcionan documentacin del proceso.  
        p(data-aos="fade-right") En el proceso de aprendizaje es importante conocer la simbologa del mapa de procesos o diagrama de procesos, con el fin de que cuando se haga uso de este tipo de herramienta se puedan aplicar los conocimientos de forma correcta.
        p.mb-0(data-aos="fade-right") Los smbolos primordiales se utilizan en un mapa de procesos para describir los elementos clave del proceso. Cada elemento del proceso est representado por un smbolo especfico, como: una flecha, un crculo, un rombo o flecha de bloque, un cuadro, un valo o un rectngulo que se utilizan para trazar o dibujar mapas de procesos, a continuacin, se presenta cada uno de ellos, cul es su forma grfica y lo que representan dentro del diseo:

    SlyderF.mb-5(columnas="col-lg-6 col-xl-4" data-aos="zoom-in")
      .tarjeta.color-primario.h-100.p-4
        .row.justify-content-center.mb-4
          .col-8
            img(src='@/assets/curso/tema1/img85.svg' alt='Imagen decorativa')
        .text-white
          .h4.text-center valos
          p.mb-0 Expresan el comienzo de un proceso o la finalizacin de un proceso.
      .tarjeta.color-primario.h-100.p-4
        .row.justify-content-center.mb-4
          .col-8
            img(src='@/assets/curso/tema1/img86.svg' alt='Imagen decorativa')
        .text-white
          .h4.text-center Rectngulos
          p.mb-0 Expresan una operacin o actividad que debe realizarse.
      .tarjeta.color-primario.h-100.p-4
        .row.justify-content-center.mb-4
          .col-8
            img(src='@/assets/curso/tema1/img87.svg' alt='Imagen decorativa')
        .text-white
          .h4.text-center Flechas (lnea de flujo)
          p.mb-0 Representan el flujo de direccin, la conexin entre los pasos.
      .tarjeta.color-primario.h-100.p-4
        .row.justify-content-center.mb-4
          .col-8
            img(src='@/assets/curso/tema1/img88.svg' alt='Imagen decorativa')
        .text-white
          .h4.text-center Flecha punteada
          p.mb-0 Se maneja cuando la relacin entre procesos no es inmediata. Solo sale una flecha de un cuadro de actividad. Conexin restringida de procesos.
      .tarjeta.color-primario.h-100.p-4
        .row.justify-content-center.mb-4
          .col-8
            img(src='@/assets/curso/tema1/img89.svg' alt='Imagen decorativa')
        .text-white
          .h4.text-center Decisin
          p.mb-0 El smbolo expresa que se debe tomar una decisin y continuar una direccin debido a esta decisin.
      .tarjeta.color-primario.h-100.p-4
        .row.justify-content-center.mb-4
          .col-8
            img(src='@/assets/curso/tema1/img90.svg' alt='Imagen decorativa')
        .text-white
          .h4.text-center Atraso
          p.mb-0 Representa que pasar algn tiempo antes de que el flujo de proceso contine.
      .tarjeta.color-primario.h-100.p-4
        .row.justify-content-center.mb-4
          .col-8
            img(src='@/assets/curso/tema1/img91.svg' alt='Imagen decorativa')
        .text-white
          .h4.text-center Datos
          p.mb-0 Expresa las entradas o salidas de cualquier dato utilizado en el mapa de procesos.
      .tarjeta.color-primario.h-100.p-4
        .row.justify-content-center.mb-4
          .col-8
            img(src='@/assets/curso/tema1/img92.svg' alt='Imagen decorativa')
        .text-white
          .h4.text-center Documento
          p.mb-0 Informacin que puede ser leda, archivo o documento que se estima en el flujo del proceso. 
      .tarjeta.color-primario.h-100.p-4
        .row.justify-content-center.mb-4
          .col-8
            img(src='@/assets/curso/tema1/img93.svg' alt='Imagen decorativa')
        .text-white
          .h4.text-center Conector
          p.mb-0 Expresa que hay una inspeccin en este punto.

    p(data-aos="fade-right") Ahora es indispensable aprender a realizar un mapa de procesos y a aplicar los conocimientos adquiridos mediante el uso de herramientas tecnolgicas que permitan disear un grfico de este tipo para cualquier tipo de empresa. 
    p.mb-5(data-aos="fade-right") Los mapas de procesos se pueden crear en programas comunes como: Microsoft Word, PowerPoint o Excel, pero existen otros programas ms personalizados para crear un mapa de procesos, como: Lucidchart.

    .row.justify-content-center.mb-4
      .col-lg-10
        .BG02.p-4
          .row.justify-content-center.align-items-center
            .col-lg-8.order-lg-1.order-2
              p(data-aos="fade-right") Cualquiera de ellos permite comunicar el proceso a otros para lograr los objetivos de gestin, por lo que se pueden usar las siguientes pautas:
              ul.lista-ul--color
                li(style="margin-bottom: 2px" data-aos="fade-left")
                  i.fas.fa-chevron-circle-right.tcp
                  | Iniciar la creacin del mapa con el proceso que precisa ser representado.
                li(style="margin-bottom: 2px" data-aos="fade-left")
                  i.fas.fa-chevron-circle-right.tcp
                  | Especificar el nombre del diagrama de procesos en el instrumento.
                li(style="margin-bottom: 2px" data-aos="fade-left")
                  i.fas.fa-chevron-circle-right.tcp
                  | Los pasos y actividades involucradas en el proceso.
                li(style="margin-bottom: 2px" data-aos="fade-left")
                  i.fas.fa-chevron-circle-right.tcp
                  | Establecer cuando se hace, quien lo hace.
                li(style="margin-bottom: 2px" data-aos="fade-left")
                  i.fas.fa-chevron-circle-right.tcp
                  | Describir el inicio del proceso, donde se paraliza o finaliza.
                li(style="margin-bottom: 2px" data-aos="fade-left")
                  i.fas.fa-chevron-circle-right.tcp
                  | Hacer uso de los smbolos para ayudar a comprender el proceso. 
              p.mb-0(data-aos="fade-right") En conclusin, el mapa de procesos presenta visualmente la informacin para comprender y mejorar la eficiencia en los mtodos de la organizacin.
            .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4
              img(src='@/assets/curso/tema1/img94.png' alt="Imagen decorativa")

    separador
    #t_1_5.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.5 Tercerizacin de servicios
    
    p(data-aos="fade-right") La subcontratacin es una prctica comercial en la que  los servicios o las funciones laborales se subcontratan a un tercero. Colombia tiene una industria de subcontratacin de procesos de negocios reconocida regionalmente, con amplia experiencia en reas comerciales, como: centros de contacto, cobranza, provisin de tecnologa interna, <i>marketing</i>, auditora y consultora. 
    p.mb-5(data-aos="fade-right") Estos servicios se prestan en industrias tan diversas, como: banca y servicios financieros, telecomunicaciones, consumo masivo, salud, gobierno, hidrocarburos, seguros, servicios pblicos, logstica, entre otras.

    .bloque-texto-g.color-primario.p-3.p-sm-4.p-md-5.mb-5
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/tema1/img95.jpg')})`}" data-aos="fade-right"
      )
      .bloque-texto-g__texto.p-4(data-aos="fade-left")
