    p(data-aos="fade-right") La tercerizacin conocida en el mundo global como <i>outsourcing</i> se convirti en una parte integral de la economa empresarial incentivando para que las empresas gestionen recursos donde sean ms eficientes. Por tanto, estas contratan los servicios de un tercero que les contribuya a la realizacin de una actividad o funcin  secundaria, para que la empresa siga concentrndose en aspectos mucho ms trascendentales.
