    .row.justify-content-center 
      p Pero, por cunto tiempo se hace un proceso de outsourcing para una empresa, sera la pregunta que se debe responder ahora. La respuesta depende de qu se subcontrata y por qu, por lo que, un acuerdo de subcontratacin que genere grandes cambios puede requerir ms tiempo para obtener beneficios tanto para el cliente como para el proveedor, pero cuando se subcontrata el mantenimiento de escritorio una relacin ms corta puede funcionar mejor. 
      p En trminos generales, se deben evitar los contratos demasiado largos es decir aquellos que oscilan por ms de cinco aos a menos que haya una gran flexibilidad contemplada en el contrato. 
