          p.mb-0(numero="1" titulo="<i>Outsourcing</i> profesional") Se puede acceder a una amplia gama de servicios, solventando solo los que necesita. Esto es excelente para hacer crecer su negocio, as como para recibir servicios ideales. Los servicios profesionales son esenciales, ya que al menos necesitar unos cuantos previo a la constitucin de la empresa.<br><br>
            | Incluye todos los servicios especializados, como: contabilidad, legal, <i>marketing</i> y una firma contable para asistencia financiera.
          p.mb-0(numero="2" titulo="Subcontratacin de Tecnologas de la Informacin") Es una muy buena opcin para todos los negocios que desean subcontratar tareas de desarrollo de TI a trabajadores remotos o desarrolladores en algn momento. <br><br>
            | Incluso las nuevas empresas necesitan soporte de TI para mantener la seguridad, las licencias actualizadas y administrar sus redes, y esta es una forma asequible de recibir esos servicios. En este tipo de <i>outsourcing</i> puede recibir soporte desde infraestructura hasta desarrollo de aplicaciones. Por ejemplo: Ingenieros de <i>software</i>, consultora de <i>software</i>, profesionales de TI.
          p.mb-0(numero="3" titulo="Subcontratacin de manufactura") Las grandes corporaciones como Apple subcontratan la produccin para reducir los costos de pago de una manufactura, trabajadores, equipos, infraestructura y materias primas. <br><br>
            | La subcontratacin de la fabricacin se est convirtiendo en una necesidad para las empresas de todos los tamaos puesto que es fcil encontrar un especialista en su industria y es importante para acelerar los tiempos de produccin manteniendo la alta calidad. <br><br>
            | En este tipo de <i>outsourcing</i> puede contar con que la fabricacin de funciones sin problemas y que los artculos se entreguen a tiempo. Por ejemplo: productos fabricados en pases, como: china que se conocen de donde proceden por su etiqueta.
          p.mb-0(numero="4" titulo="<em><i>Outsourcing</i></em> de proyectos") Cuando la empresa carece de tiempo, de habilidades o de recursos, subcontrata servicios de proyectos que incluyen el diseo del sitio web, la creacin de una gran parte del contenido y las campaas de mercadeo. <br><br>
            | Es mucho ms econmico contratar servicios remotos para cumplir con un proyecto que vincular a una persona, ya que al contratar servicios remotos est contratando experiencia, calidad, habilidades y conocimiento para estructurar el proyecto en cuestin de segundos en lugar de alguien que requiere de capacitacin para desarrollar sus labores y que tardara en entregar el proyecto. Por ejemplo: empresas de gestin de proyectos.

